.spin-game {
  text-align: center;
  color: #fff;
  background-color: #001f3f;
  padding: 20px;
  border-radius: 10px;
  width: 400px;
  margin: auto;
  font-family: Arial, sans-serif;
}

.header {
  margin-bottom: 20px;
}

.spin-container {
  background: linear-gradient(to bottom, #004282, #1a91ff, #004282);
}

.prefix-bg {
  background: linear-gradient(to bottom, #004282 0%, #1a91ff 10%, #1a91ff 90%, #004282 100%);
}

.spinner {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
  height: 60px; /* Match the height of the spinning elements */
  overflow: hidden; /* Hide overflow */
}

.counter-container {
  display: flex;
  overflow: hidden;
}

.counter-char {
  background: linear-gradient(to bottom, #004282, #1a91ff, #004282);
  padding: 0; /* Remove padding to match the exact size */
  border-radius: 10px; /* Slightly rounded corners */
  width: 96px; /* Adjust width if needed */
  height: 120px; /* Exact height to fit one character */
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 48px; /* Increase font size */
  box-sizing: border-box; /* Include padding in the height and width */
  line-height: 120px; /* Ensure single line height matches container height */
}

button {
  background-color: #007bff;
  border: none;
  padding: 10px 20px;
  color: white;
  font-size: 1em;
  cursor: pointer;
  border-radius: 5px;
}

button:hover {
  background-color: #0056b3;
}

.libertycarz {
  margin-top: 20px;
  font-size: 0.8em;
}

@media screen and (min-width: 1025px) and (max-width: 1536px) {
  .zoom_container {
    zoom: 0.8;
  }
  .counter-container {
    display: flex;
    overflow: hidden;
    gap: 8px;
  }

  .counter-char {
    background: linear-gradient(to bottom, #004282, #1a91ff, #004282);
    padding: 0; /* Remove padding to match the exact size */
    border-radius: 8px; /* Slightly rounded corners */
    width: 76.8px; /* Adjust width if needed */
    height: 96px; /* Exact height to fit one character */
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 38.4px; /* Increase font size */
    box-sizing: border-box; /* Include padding in the height and width */
    line-height: 96px; /* Ensure single line height matches container height */
  }
}

.lever-t-top-to-bottom-disappear-animate {
  animation: slideDownDisappear 0.3s forwards;
}

.lever-t-bottom-to-top-appear-animate {
  animation: slideUpAppear 0.3s forwards;
}

.lever-b-top-to-bottom-appear-animate {
  animation: slideDownAppear 0.3s forwards;
}

.lever-b-bottom-to-top-disappear-animate {
  animation: slideUpDisappear 0.3s forwards;
}

@keyframes slideDownDisappear {
  0% {
    transform: translateY(0);
    opacity: 1;
  }
  100% {
    transform: translateY(30%);
    opacity: 0;
  }
}

@keyframes slideDownAppear {
  0% {
    transform: translateY(0);
    opacity: 0;
  }
  100% {
    transform: translateY(30%);
    opacity: 1;
  }
}

@keyframes slideUpDisappear {
  0% {
    transform: translateY(30%);
    opacity: 1;
  }
  100% {
    transform: translateY(0);
    opacity: 0;
  }
}

@keyframes slideUpAppear {
  0% {
    transform: translateY(30%);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

.prize-animate-end {
  animation: slideToLeftDisappear 0.5s forwards;
}

.prize-animate-start {
  animation: slideToLeftAppear 0.5s backwards;
}

@keyframes slideToLeftAppear {
  0% {
    transform: translateX(-100%);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes slideToLeftDisappear {
  0% {
    transform: translateX(0);
    opacity: 1;
  }
  100% {
    transform: translateX(100%);
    opacity: 0;
  }
}

.flicker-animate {
  animation: flicker 1s;
}
@keyframes flicker {
  0% {
    opacity: 1;
  }
  25% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  75% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.congratulation-animate {
  animation: congratulation 0.7s linear;
}

@keyframes congratulation {
  0% {
    width: 0;
  }
  100% {
    width: 700px;
  }
}

.congratulation-animate-item {
  animation: congratulation-item 0.9s linear;
}

@keyframes congratulation-item {
  0% {
    width: 0;
    height: 0;
    font-size: 0;
  }
  100% {
    width: 68px;
    height: 68px;
    font-size: 48px;
  }
}

.congratulation-animate-tk {
  animation: congratulation-tk 0.9s linear;
}

@keyframes congratulation-tk {
  0% {
    width: 0;
    height: 0;
    font-size: 0;
  }
  100% {
    width: 68px;
    height: 68px;
    font-size: 36px;
  }
}

.congratulation-animate-title {
  animation: congratulation-title 0.9s linear;
}

@keyframes congratulation-title {
  0% {
    font-size: 0;
  }
  100% {
    font-size: 24px;
  }
}
